import React, { Component } from 'react';

export default class Delete extends Component{

    selectItem(){
        const { setCurrentItem } = this.props.toolsActions;
        setCurrentItem(1);
        alert('2');
    }

    render(){
        let temp;
        if(this.props.tools.currentItem === 1)
            temp = 'active hand';
        return(
            <div
                className = {temp}
                onClick={this.selectItem.bind(this)}
            >
                Delete
            </div>
        );
    }
}